import React, {useContext, useEffect, useState} from '@pika/react';
import {Button, Col, Modal, Row, Spin} from 'antd';
import {RateComponent} from "./rate";
import {LoginContext} from "../../contexts/login.context";
import axios from "axios";
import {API_HOST} from "../../utils/constants";

export const RightComponent = props => {
    const {rateInfo} = props
    const [roomToken, setRoomToken] = useState("");
    const {userInfo} = useContext(LoginContext);
    const [isHost, setIsHost] = useState(userInfo.name === rateInfo.creator_name);
    const [quitVisible, setQuitVisible] = useState(false);
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        let loc = window.location.pathname.split("/");
        let rToken = loc[loc.length - 1];
        setRoomToken(rToken);
    }, []);

    const quit = () => {
        setLoading(true);
        axios.post(API_HOST + '/api/v1/room/' + roomToken + '/end')
            .then(({data}) => {
                console.log(data);
                setLoading(false);
                setQuitVisible(false)
                window.location.reload(false);
            })
    };
    return (
        <div>
            <Modal
                title="确认结束？"
                visible={quitVisible}
                onOk={quit}
                onCancel={() => {
                    setQuitVisible(false)
                }}
            >
                <Spin spinning={loading}>
                    <p>确认结束本次面试吗？</p>
                    <p>结束后面试不可以恢复。</p>
                    <p>请在结束之前确认您的代码已经提交。</p>
                </Spin>
            </Modal>
            <Row align={"middle"} justify={"space-around"} style={{padding: "10px"}}>
                <Col flex={1}/>
                <Col flex={3}>
                    <Row gutter={[16, 16]} align={"middle"} justify={"space-around"}>
                        <Col flex={"25%"}>
                            面试官:
                        </Col>
                        <Col flex={"25%"}>
                            {rateInfo.creator_name}
                        </Col>
                        <Col flex={"25%"}>
                            候选人:
                        </Col>
                        <Col flex={"25%"}>
                            {rateInfo.guest_name}
                        </Col>
                    </Row>
                    <Row gutter={[16, 16]} align={"middle"}>
                        <Col>
                            {
                                isHost &&
                                <Button onClick={() => {
                                    setQuitVisible(true)
                                }}>结束面试</Button>
                                ||
                                <Button onClick={() => {
                                    setQuitVisible(true)
                                }}>退出面试</Button>
                            }
                        </Col>
                    </Row>
                    {isHost && <RateComponent rateInfo={rateInfo}/>}
                </Col>
                <Col flex={1}/>
            </Row>
        </div>
    );
};
