import React, {useContext, useEffect, useState} from '@pika/react';
import {NavContext} from 'app/contexts/nav.context.js';
import {message, Popconfirm, Space, Table, Tabs} from 'antd';
import {STATUS_MAP} from 'app/utils/constants.js';
import axios from "axios";
import {API_HOST} from "app/utils/constants";
import {LoginContext} from "../../contexts/login.context";

const {TabPane} = Tabs;

export const MyCreatedPage = props => {
    const {refreshBoard, setRefreshBoard} = props;
    const context = useContext(NavContext);
    const {setSelectedMenu} = context;
    const { isLoginDialogShow, setLoginDialogShow} = useContext(LoginContext);
    const [pagination, setPagination] = useState({page: 1, perPage: 10});
    const [interviewList, setInterviewList] = useState([]);
    const [total, setTotal] = useState(0);

    useEffect(() => {
        if (isLoginDialogShow === true) return
        setSelectedMenu('interviews');
        console.log("isLoginDialogShow " + isLoginDialogShow)
        // fetch data from remote
        axios.get(API_HOST + '/api/v1/board')
            .then(({data}) => {
                console.log(data);
                setInterviewList(data['data']);
                setRefreshBoard(false)
            })
            .catch(function (error) {
                if (error.response && error.response.status === 401) {
                    // setLoginDialogShow(true);
                } else {
                    console.log(error)
                }
            });
    }, [isLoginDialogShow, refreshBoard]);
    const cols = [
        {
            title: '面试板',
            dataIndex: 'name',
            key: 'name'
        },
        {
            title: '状态',
            dataIndex: 'status',
            key: 'status',
            render(status, record) {
                console.log(record.duration)
                if (status === 2) {
                    return `${STATUS_MAP[status]} (${record.duration}分钟)`;
                }
                return `${STATUS_MAP[status]}`;
            }
        },
        {
            title: '参与者',
            dataIndex: 'attendee',
            key: 'attendee'
        },
        {
            title: '创建时间',
            dataIndex: 'created_at',
            key: 'created_at'
        },
        {
            title: '切屏次数',
            dataIndex: 'leaves',
            key: 'leaves'
        },
        {
            title: '语言',
            dataIndex: 'language',
            key: 'language'
        },
        {
            title: '得分',
            dataIndex: 'score',
            key: 'score'
        },
        {
            title: '操作',
            key: 'action',
            render(text, record) {
                let link;
                if (record.status === 0) {
                    link = <a onClick={() => {
                        window.open(API_HOST + '/room/' + record.token)
                    }}>进入</a>;
                } else {
                    link = <a onClick={() => {
                        message.info(API_HOST + '/api/v1/room/' + record.token)
                    }}>回放</a>;
                }
                return (
                    <Space size="middle">
                        {link}
                        <Popconfirm
                            placement="bottomRight"
                            title="确认删除面试板？"
                            onConfirm={() => {
                                console.log("del")
                                axios.delete(API_HOST + '/api/v1/room/' + record.token)
                                    .then(({data}) => {
                                        console.log(data);
                                        message.info('删除成功');
                                        setRefreshBoard(true)
                                    })
                            }}
                            okText="确认"
                            cancelText="取消"
                        >
                            <a>删除</a>
                        </Popconfirm>
                    </Space>
                )
            }
        }
    ];
    return (
        <div className="page">
            <Table
                dataSource={interviewList}
                columns={cols}
                pagination={{
                    defaultCurrent: 1,
                    current: pagination.page,
                    pageSize: pagination.perPage,
                    pageSizeOptions: [10, 20, 50, 100],
                    showSizeChanger: true,
                    total: total,
                    onChange: (page, perPage) => {
                        // TODO
                    },
                    onShowSizeChange: (current, perPage) => {
                        // TODO
                    }
                }}
            />
        </div>
    );
};
