import React, {useEffect, useState} from '@pika/react';
import {Col, Input, Rate, Row} from 'antd';
import axios from "axios";
import {API_HOST} from "../../utils/constants";

export const RateComponent = props => {
    const {rateInfo} = props
    const [roomToken, setRoomToken] = useState("");

    useEffect(() => {
        let loc = window.location.pathname.split("/");
        let rToken = loc[loc.length - 1];
        setRoomToken(rToken);
        console.log(rateInfo)
    }, []);

    const textChange = (text, type) => {
        console.log(text, type, roomToken)
        axios.post(API_HOST + '/api/v1/exam/' + type + "/text",
            {roomToken, text})
            .then(({data}) => {
                console.log(data);
            })
    }
    const rateChange = (rate, type) => {
        console.log(rate, type, roomToken)
        axios.post(API_HOST + '/api/v1/exam/' + type + "/rate",
            {roomToken, rate})
            .then(({data}) => {
                console.log(data);
            })
    }
    return (
        <div>
            <Row gutter={[16, 16]} align={"middle"} justify={"start"}>
                <Col flex={"90px"} align={"left"}>
                    代码质量:
                </Col>
                <Col>
                    <Rate allowHalf
                          defaultValue={rateInfo.rate_quality}
                          onChange={(value) => {
                              rateChange(value, "quality");
                          }}
                    />
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"} justify={"start"}>
                <Col flex={"256px"}>
                    <Input.TextArea rows={4} defaultValue={rateInfo.text_quality} onChange={(e) => {
                        textChange(e.target.value, "quality");
                    }}/>
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"} justify={"start"}>
                <Col flex={"90px"} align={"left"}>
                    解决问题:
                </Col>
                <Col>
                    <Rate allowHalf defaultValue={rateInfo.rate_resolve} onChange={(value) => {
                        rateChange(value, "resolve");
                    }}/>
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"}>
                <Col flex={"256px"}>
                    <Input.TextArea rows={4} defaultValue={rateInfo.text_resolve} onChange={(e) => {
                        textChange(e.target.value, "resolve");
                    }}/>
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"}>
                <Col flex={"90px"} align={"left"}>
                    语言熟练度:
                </Col>
                <Col>
                    <Rate allowHalf defaultValue={rateInfo.rate_skill} onChange={(value) => {
                        rateChange(value, "skill");
                    }}/>
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"}>
                <Col flex={"256px"}>
                    <Input.TextArea rows={4} defaultValue={rateInfo.text_skill} onChange={(e) => {
                        textChange(e.target.value, "skill");
                    }}/>
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"}>
                <Col flex={"90px"} align={"left"}>
                    沟通能力:
                </Col>
                <Col>
                    <Rate allowHalf defaultValue={rateInfo.rate_communicate} onChange={(value) => {
                        rateChange(value, "communicate");
                    }}/>
                </Col>
            </Row>
            <Row gutter={[16, 16]} align={"middle"}>
                <Col flex={"256px"}>
                    <Input.TextArea rows={4} defaultValue={rateInfo.text_communicate} onChange={(e) => {
                        textChange(e.target.value, "communicate");
                    }}/>
                </Col>
            </Row>
        </div>
    );
};
