import React, {useEffect, useState} from '@pika/react';
import {Col, List, Progress, Row, Skeleton, Spin} from 'antd';
import SockJsClient from 'react-stomp';
import Cookies from 'js-cookie';
import {API_HOST} from "../../utils/constants";
import axios from "axios";

export const LandingPage = props => {
    const {setVsCodeToken} = props
    const [clientRef, setClientRef] = useState(null);
    const [dataList, setDataList] = useState(['面试房间正在启动中...']);
    const [progress, setProgress] = useState(0);
    const consoleFooter = <Skeleton active={true} paragraph={false} title={true}/>;
    // const [vsCodeUrl, setVsCodeUrl] = useState("");
    const onMessage = (msg) => {
        console.log("rec: " + msg.value, msg.progress, msg.vscodeToken);
        setProgress(msg.progress);
        if (msg.vscodeToken !== null && msg.vscodeToken !== '') {
            setSocketStart(false);
            console.log(msg.vscodeToken);
            setDataList([...dataList, "正在下载面试环境..."])
            // setVsCodeUrl("http://18.222.179.214:9099/w/" + msg.vscodeToken + "/?folder=/root");
            axios.get("http://cdn.aladdinrank.com/workbench.web.api.nls.js"
                // , {
                // withCredentials: false,
                // headers: {
                //     'Access-Control-Allow-Origin': '*',
                //     'Access-Control-Allow-Methods': 'GET',
                // }
            // }
        )
                .then(({data}) => {
                    setVsCodeToken(msg.vscodeToken);
                    setDataList([...dataList, "面试环境已经下载完成。"]);
                    setProgress(100);
                })
                .catch((e) => {
                    // setDataList([...dataList, "面试已经结束，请联系面试官或重新创建面试。"]);
                    // setSocketStart(false);
                    setVsCodeToken(msg.vscodeToken);
                    setDataList([...dataList, "面试环境已经下载完成。"]);
                });
        }
        if (msg.value !== '') {
            setDataList([...dataList, msg.value])
        }
    }

    const [socketStart, setSocketStart] = useState(false);
    const [shareLinkToken, setShareLinkToken] = useState("");
    useEffect(() => {
        let loc = window.location.pathname.split("/");
        let roomToken = loc[loc.length - 1];
        setShareLinkToken(roomToken)
        axios.get(API_HOST + '/api/v1/room/' + roomToken)
            .then(({data}) => {
                console.log(data);
                setSocketStart(true)
            })
    }, [])
    const socketJsClient = <SockJsClient url={API_HOST + '/ws/endpoint'}
                                         topics={[
                                             '/ws/user/' + Cookies.get('wsToken') + '/env/process'
                                         ]}
                                         onMessage={onMessage}
                                         ref={setClientRef}
                                         onConnect={() => {
                                             clientRef.sendMessage('/ws/push/env',
                                                 JSON.stringify(
                                                     {
                                                         "userToken": Cookies.get('wsToken'),
                                                         "shareLinkToken": shareLinkToken,
                                                     }
                                                 )
                                             );
                                         }}/>
    return (
        <div className="room-page page">
            <div style={{display: "none"}}>
                {socketStart && socketJsClient}
            </div>
            <div style={{padding: "100px"}}>
                <Row>
                    <Col flex={5}/>
                    <Col flex={5}>
                        <Row>
                            <Col flex={1}/>
                            <Col flex={"center"}>
                                <Spin size="large"/>
                            </Col>
                            <Col flex={1}/>
                        </Row>
                        <Row>
                            <Col flex={"auto"}>
                                <Progress percent={progress} status="active" showInfo={false} strokeColor={{
                                    '0%': '#108ee9',
                                    '100%': '#87d068',
                                }}/>
                            </Col>
                        </Row>
                        <Row>
                            <Col flex={"auto"}>
                                <List
                                    size="small"
                                    // footer={0 < progress < 100 && consoleFooter}
                                    bordered
                                    dataSource={dataList}
                                    renderItem={item => <List.Item>{item}</List.Item>}
                                />

                            </Col>
                        </Row>
                    </Col>
                    <Col flex={5}/>
                </Row>
            </div>
        </div>
    );
};
