import React, {useContext, useState} from '@pika/react';
import {Button, Dropdown, Layout, Menu} from 'antd';
import {BrowserRouter as Router, Link, Redirect, Route, Switch} from 'react-router-dom';
import {
    LogoutOutlined,
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    ProfileFilled,
    SettingOutlined,
    UserOutlined
} from '@ant-design/icons';
import {NavContext} from 'app/contexts/nav.context.js';
import {InterviewsPage} from './interviews';
import {APITest} from "./apitest";
import axios from 'axios';
import {API_HOST} from "../utils/constants";
import {SettingComponent} from "./setting";
import {LoginContext} from "../contexts/login.context";

const {Header, Sider, Content} = Layout;

export const IndexComponent = props => {
    const {setLoginDialogShow, userInfo} = useContext(LoginContext);

    const [selectedMenu, setSelectedMenu] = useState('');
    const [collapsed, setCollapsed] = useState(false);
    const CollapseBtn = collapsed ? MenuUnfoldOutlined : MenuFoldOutlined;

    const userMenu = (
        <Menu>
            <Menu.Item icon={<SettingOutlined/>}>
                <Link to="/board/setting">
                    设置
                </Link>
            </Menu.Item>
            <Menu.Item icon={<LogoutOutlined/>} onClick={() => {
                axios.get(API_HOST + '/api/v1/account/logout')
                    .then(({data}) => {
                        console.log(data);
                        // setLoginDialogShow(true)
                        window.location.reload(false);
                    })
            }}>
                退出
            </Menu.Item>
        </Menu>
    );

    const handleMenuClick = e => {
        console.log('click', e)
        setSelectedMenu(e.key)
    }

    return (
        <NavContext.Provider value={{setSelectedMenu}}>
            <div className="index-page">
                <Router>
                    <Layout className="index-layout">
                        <Sider trigger={null} collapsible collapsed={collapsed}>
                            <div className="sidebar-logo">阿拉丁远程面试平台</div>
                            <Menu theme="dark" mode="inline" selectedKeys={[selectedMenu]} onClick={handleMenuClick}>
                                <Menu.Item key="interviews" icon={<ProfileFilled/>}>
                                    <Link to="/board/interviews">
                                        面试板
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="apitest" icon={<ProfileFilled/>}>
                                    <Link to="/board/apitest">
                                        API Test
                                    </Link>
                                </Menu.Item>
                            </Menu>
                        </Sider>
                        <Layout>
                            <Header className="site-header">
                                <CollapseBtn className="sidebar-trigger" onClick={() => setCollapsed(!collapsed)}/>
                                <div className="space"/>
                                <div className="user-info">
                                    <Dropdown overlay={userMenu} placement="bottomRight">
                                        <Button
                                            type="text"
                                            icon={<UserOutlined/>}>
                                            {userInfo !== null && userInfo.name}
                                        </Button>
                                    </Dropdown>
                                </div>
                            </Header>
                            <Content className="site-content">
                                <div className="site-content-main">
                                    <Switch>
                                        <Route path="/board" exact>
                                            <Redirect to="/board/interviews"/>
                                        </Route>
                                        <Route path="/board/apitest">
                                            <APITest/>
                                        </Route>
                                        <Route path="/board/interviews">
                                            <InterviewsPage/>
                                        </Route>
                                        <Route path="/board/setting">
                                            <SettingComponent/>
                                        </Route>
                                    </Switch>
                                </div>
                            </Content>
                        </Layout>
                    </Layout>
                </Router>
            </div>
        </NavContext.Provider>
    );
};
