import React, {useEffect, useState} from '@pika/react';
import {IndexComponent} from './components';
import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom";
import {RoomPage} from "./components/room/";
import {LoginContext} from "./contexts/login.context";
import {WelcomeBg} from "./components/welcome";
import axios from "axios";
import {API_HOST} from "./utils/constants";

function App() {
    const [isLoginDialogShow, setLoginDialogShow] = useState(false);
    const [userInfo, setUserInfo] = useState({});
    const [loginStatus, setLoginStatus] = useState(true);
    const [pathname, setPathname] = useState("");
    useEffect(() => {
        axios.get(API_HOST + '/api/v1/user/')
            .then(({data}) => {
                console.log(data);
                setUserInfo(data['data']);
                setLoginDialogShow(false);
                setLoginStatus(true);
            }).catch((e) => {
            setLoginStatus(false);
        })
    }, []);

    const loginRoute = (node) => {
        if (loginStatus) {
            return node;
        } else {
            return <WelcomeBg/>;
        }
    };
    return (
        <div className="app-container">
            <Router>
                <Switch>
                    <LoginContext.Provider value={{userInfo, isLoginDialogShow, setLoginDialogShow, pathname}}>
                        {/*<LoginDialog/>*/}
                        <Route path="/" exact>
                            {Object.keys(userInfo).length > 0 && loginStatus && <Redirect to={"/board"}/> ||
                            <WelcomeBg/>}
                        </Route>
                        <Route path="/login" exact>
                            <WelcomeBg/>
                        </Route>
                        <Route path="/board">
                            {loginRoute(<IndexComponent/>)}
                        </Route>
                        <Route path="/room">
                            {loginRoute(<RoomPage/>)}
                        </Route>
                    </LoginContext.Provider>
                </Switch>
            </Router>
        </div>
    );
}

export default App;
