import React, {useEffect, useState} from '@pika/react';
import {LandingPage} from "./landing";
import {VSCodeComponent} from "./vscode";
import {Button, Result} from "antd";
import axios from "axios";
import {API_HOST} from "../../utils/constants";


export const RoomPage = props => {
    const [vsCodeToken, setVsCodeToken] = useState("");
    const [mainContent, setMainContent] = useState(<LandingPage setVsCodeToken={setVsCodeToken}/>)
    useEffect(() => {
        let loc = window.location.pathname.split("/");
        let rToken = loc[loc.length - 1];
        axios.get(API_HOST + '/api/v1/room/' + rToken)
            .then(({data}) => {
                console.log(data);
                if (data.data.status === 2) {
                    setMainContent(<Result title="面试已经结束，请看回放。"/>,)
                }
            })
    }, []);
    useEffect(() => {
        if (vsCodeToken === "error") {
            setMainContent(<Result
                title="面试者还未进入，请等待。"
                extra={
                    <Button type="primary" key="console"
                            onClick={() => {
                                window.location.reload(true);
                            }}>
                        刷新
                    </Button>
                }
            />,)
        } else if (vsCodeToken !== "" && vsCodeToken !== null) {
            setMainContent(<VSCodeComponent vsCodeToken={vsCodeToken}/>)
        }
    }, [vsCodeToken]);
    return (
        <div className="room-page page">
            {mainContent}
        </div>
    );
};
