import React, {useState} from '@pika/react';
import {Button, Col, Form, Input, message, Modal, Row, Select} from 'antd';
import axios from "axios";
import CopyToClipboard from "react-copy-to-clipboard";
import {API_HOST} from "../../utils/constants";

const {Option} = Select;

export const ShareLinkDialog = props => {
    const {visible, onClose} = props;

    const [shareLink, setShareLink] = useState("");
    const [form] = Form.useForm();
    const createShareLink = () => {
        form.validateFields()
            .then(() => {
                    const {title, env} = form.getFieldsValue();
                    axios.post(API_HOST + '/api/v1/room/sharelink', {title, env})
                        .then(({data}) => {
                            console.log(data);
                            setShareLink(data['data']['link']);
                        })
                }
            );
    }
    return (
        <Modal visible={visible} footer={null} onCancel={onClose}>
            <div style={{padding: '30px'}}>
                <Form
                    form={form}
                    name="sharelink"
                    style={{margin: '0 auto'}}
                >
                    <Row justify="space-around" align="middle" style={{marginTop: '10px'}}>
                        <Col flex="100px" align="right">
                            标题：
                        </Col>
                        <Col flex={5}>
                            <Form.Item
                                name="title"
                                rules={[{required: true, message: '请输入面试板名称'}]}
                                style={{margin: '0 auto'}}
                                initialValue="未命名面试板"
                            >
                                <Input placeholder="面试板标题" id="title"/>
                            </Form.Item>
                        </Col>
                    </Row>
                    <Row justify="space-around" align="middle" style={{marginTop: '10px'}}>
                        <Col flex="100px" align="right">
                            环境：
                        </Col>
                        <Col flex={5}>
                            <Form.Item
                                name="env"
                                rules={[{required: true, message: '请选择面试环境'}]}
                                style={{margin: '0 auto'}}
                            >
                                <Select placeholder="面试环境" style={{width: '100%'}}>
                                    <Option value="java">Java</Option>
                                    <Option value="python2">Python 2</Option>
                                    <Option value="c">C</Option>
                                    <Option value="c++">C++</Option>
                                </Select>
                            </Form.Item>
                        </Col>
                    </Row>
                </Form>
                <Row justify="space-around" align="middle" style={{marginTop: '10px'}}>
                    <Col flex="100px" align="right">
                    </Col>
                    <Col flex={5}>
                        <Button type={"primary"} htmlType="submit" style={{width: '100%'}}
                                onClick={createShareLink}
                        >
                            生成分享地址
                        </Button>
                    </Col>
                </Row>
                <Row justify="space-around" align="middle" style={{marginTop: '10px'}}>
                    <Col flex="100px" align="right">
                        分享地址：
                    </Col>
                    <Col flex={5}>
                        <Input placeholder="面试板分享地址" value={shareLink}/>
                    </Col>
                    <Col flex={1}>
                        <CopyToClipboard text={shareLink}>
                            <Button style={{width: '100%'}} onClick={() => {
                                message.info("复制成功")
                            }}>复制</Button>
                        </CopyToClipboard>
                    </Col>
                </Row>
                <Row justify="space-around" align="middle" style={{marginTop: '10px'}}>
                    <Col flex={"100px"}/>
                    <Col flex={5}>
                        <Button htmlType="submit" style={{width: '100%'}}
                                onClick={onClose}
                        >
                            关闭
                        </Button>
                    </Col>
                    <Col flex={"100px"}/>
                </Row>
            </div>
        </Modal>
    );
};
