import React, {useState} from '@pika/react';
import {Button, Col, Divider, Input, Row, Upload} from 'antd';
import ImgCrop from 'antd-img-crop';
import {LoadingOutlined, PlusOutlined} from '@ant-design/icons';


export const SettingComponent = () => {
    console.log('SettingComponent');
    const [loading, setLoading] = useState(false);
    const [imageUrl, setImageUrl] = useState("");
    const getBase64 = (img, callback) => {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(img);
    }

    function beforeUpload(file) {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJpgOrPng) {
            message.error('You can only upload JPG/PNG file!');
        }
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isLt2M) {
            message.error('Image must smaller than 2MB!');
        }
        return isJpgOrPng && isLt2M;
    }

    const handleChange = info => {
        if (info.file.status === 'uploading') {
            setLoading(true)
            return;
        }
        if (info.file.status === 'done') {
            // Get this url from response in real world.
            getBase64(info.file.originFileObj, (imageUrl) => {
                    setImageUrl(imageUrl);
                    setLoading(false);
                }
            );
        }
    };
    const uploadButton = (
        <div>
            {loading ? <LoadingOutlined/> : <PlusOutlined/>}
            <div className="ant-upload-text">Upload</div>
        </div>
    );

    return (
        <div style={{padding: "10px"}}>
            <Divider>修改个人信息</Divider>
            <div style={{padding: "10px"}}>
                <Row gutter={[16, 16]}>
                    <Col flex={3}/>
                    <Col flex={2}>

                        <Row gutter={[16, 16]}>
                            <Col flex={"100px"}>
                                账号：
                            </Col>
                            <Col flex={"auto"}>
                                alsfdjas
                            </Col>
                        </Row>
                        <Row>
                            <Col flex={"100px"}>
                                头像：
                            </Col>
                            <Col flex={"auto"}>
                                <ImgCrop rotate>
                                    <Upload
                                        name="avatar"
                                        listType="picture-card"
                                        className="avatar-uploader"
                                        showUploadList={false}
                                        action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                                        beforeUpload={beforeUpload}
                                        onChange={handleChange}
                                    >
                                        {imageUrl ?
                                            <img src={imageUrl} alt="avatar" style={{width: '100%'}}/> : uploadButton}
                                    </Upload>
                                </ImgCrop>
                            </Col>
                        </Row>
                        <Row gutter={[16, 16]}>
                            <Col flex={"100px"}>
                                用户名：
                            </Col>
                            <Col flex={"200px"}>
                                <Input placeholder={"share link"}/>
                            </Col>
                        </Row>
                        <Row gutter={[16, 16]}>
                            <Col flex={"100px"}>
                                简历上传：
                            </Col>
                            <Col flex={"200px"}>
                                <Input placeholder={"share link"}/>
                            </Col>
                        </Row>
                        <Row gutter={[16, 16]}>
                            <Col>
                                <Button>确认</Button>
                            </Col>
                        </Row>
                    </Col>
                    <Col flex={3}/>
                </Row>
            </div>
        </div>
    );
};