import React, {useEffect, useState} from '@pika/react';
import {Col, Row} from "antd";
import axios from "axios";
import {API_HOST} from "../../utils/constants";
import Iframe from "react-iframe";
import {RightComponent} from "./right_sidebar";

import PageVisibility from 'react-page-visibility';

export const VSCodeComponent = props => {
    const {vsCodeToken} = props;
    const [vsCodeUrl, setVsCodeUrl] = useState("")
    useEffect(() => {
        setVsCodeUrl("http://18.222.179.214:9099/w/" + vsCodeToken + "/?folder=/root");
        console.log(vsCodeUrl)
    }, [])
    const [rightComponent, setRightComponent] = useState();
    const [iFrameHeight, setIFrameHeight] = useState((document.body.scrollHeight - 10) + 'px');

    useEffect(() => {
        let loc = window.location.pathname.split("/");
        let rToken = loc[loc.length - 1];
        axios.get(API_HOST + '/api/v1/exam/room/' + rToken + '/rate')
            .then(({data}) => {
                console.log(data);
                setRightComponent(<RightComponent rateInfo={data.data}/>)
            })
    }, []);
    const onPageVisibility = (visibility) => {
        console.log(visibility);
        if (visibility) return;
        let loc = window.location.pathname.split("/");
        let rToken = loc[loc.length - 1];
        axios.post(API_HOST + '/api/v1/exam/room/' + rToken + '/leave')
            .then(({data}) => {
                console.log(data);
            })
    };
    return (
        <div className={"vscode-component"}>
            <PageVisibility onChange={onPageVisibility}/>
            <Row>
                <Col flex={3}>
                    <Iframe src={vsCodeUrl}
                            width="100%"
                            height={iFrameHeight}
                            id="myId"
                            className="myClassname"
                            display="initial"
                            scrolling="no"
                            frameBorder="1"
                            position="relative"
                    />
                </Col>
                <Col flex={1}>
                    <Row>
                        <Col flex={"100%"}>
                            {rightComponent}
                        </Col>
                    </Row>
                </Col>
            </Row>
        </div>
    );
}
