import React, {useContext, useEffect, useState} from '@pika/react';
import {Alert, Button, Col, Form, Input, Layout, Row, Space, Spin} from 'antd';
import {LoginContext} from "../contexts/login.context";
import logoImg from '../assets/main_logo.svg'
import {LockOutlined, UserOutlined} from '@ant-design/icons';
import axios from "axios";
import {API_HOST} from "../utils/constants";

const {Header, Footer, Sider, Content} = Layout;


export const WelcomeBg = props => {
    const {pathname} = useContext(LoginContext);
    useEffect(() => {
    }, []);
    const [isLoading, setLoading] = useState(false);
    const [loginAlertProps, setLoginAlertProps] = useState({status: false, message: "", type: "info"})
    const loginAlert = (
        <div>
            <Alert message={loginAlertProps.message} type={loginAlertProps.type} banner/>
        </div>
    );
    const [form] = Form.useForm();
    const onSubmit = values => {
        const {username, password} = values;
        setLoading(true);
        setLoginAlertProps({status: false})

        axios.defaults.withCredentials = true
        axios.post(API_HOST + '/api/v1/account/login',
            "username=" + username + "&password=" + password
        ).then(({data}) => {
            console.log(1);
            console.log(data);
            if (data.result === 0) {
                setLoading(false);
                // setLoginDialogShow(false);
                console.log(window.location.pathname);
                // if (window.location.pathname === "") {
                //     window.location.pathname = "/board";
                // } else {
                //     window.location.reload(false);
                // }
            } else {
                setLoading(false);
                setLoginAlertProps({status: true, message: "用户名或密码错误", type: "error"})
            }
        })
    }
    const handleRegister = () => {
        setLoginAlertProps({status: false})
        form.validateFields()
            .then(() => {
                const {username, password} = form.getFieldsValue();
                console.log(username, password);
                setLoading(true);
                axios.defaults.withCredentials = true
                axios.post(API_HOST + '/api/v1/account/register',
                    {username, password}
                )
                    .then(({data}) => {
                        console.log(data);
                        if (data.result === 0) {
                            setLoginAlertProps({status: true, message: "注册成功", type: "info"});
                            setLoading(false);
                            onSubmit({username, password});
                        }
                        if (data.result === 4) {
                            setLoginAlertProps({status: true, message: "用户名已经被注册", type: "warning"});
                            setLoading(false);
                        }
                    })
            });
    };
    return (
        <div>
            <Layout>
                <Header>
                    <Row>
                        <Col>
                            <div style={{color: "white"}}>
                                Aladdin 阿拉丁面试平台
                            </div>
                        </Col>
                    </Row>
                </Header>
                <Content style={{
                    margin: "10px",
                    padding: "20px",
                    backgroundColor: "#FFFFFF"
                }}>
                    <Spin spinning={isLoading}>
                        {loginAlertProps.status && loginAlert}
                        <Row justify={"center"}>
                            <Col>
                                <img src={logoImg} alt={"logo"}/>
                            </Col>
                        </Row>
                        <Row justify={"center"}>
                            <Col>
                                <Space direction="vertical">
                                    <Space align={"center"}>
                                        <div style={{padding: '100px'}}>
                                            <Form
                                                form={form}
                                                name="normal_login"
                                                style={{maxWidth: '300px', margin: '0 auto'}}
                                                size="large"
                                                onFinish={onSubmit}
                                            >
                                                <Form.Item
                                                    name="username"
                                                    rules={[{required: true, message: '请输入用户名'}]}
                                                >
                                                    <Input prefix={<UserOutlined className="site-form-item-icon"/>}
                                                           placeholder="用户名"/>
                                                </Form.Item>
                                                <Form.Item
                                                    name="password"
                                                    rules={[{required: true, message: '请输入密码'}]}
                                                >
                                                    <Input
                                                        prefix={<LockOutlined className="site-form-item-icon"/>}
                                                        type="password"
                                                        placeholder="密码"
                                                    />
                                                </Form.Item>
                                                <Form.Item>
                                                    <Button type="primary" htmlType="submit" style={{width: '100%'}}>
                                                        登录
                                                    </Button>
                                                    <Button style={{width: '100%', marginTop: '10px'}}
                                                            onClick={handleRegister}>
                                                        注册
                                                    </Button>
                                                </Form.Item>
                                            </Form>
                                        </div>
                                    </Space>
                                </Space>
                            </Col>
                        </Row>
                    </Spin>
                </Content>
            </Layout>
        </div>
    );
};
