import React, {useState} from '@pika/react';
import {Button, PageHeader, Tabs} from 'antd';
import {ShareLinkDialog} from './sharelink';
import {MyCreatedPage} from "./my_created";
import {MyTakePartPage} from "./my_take";

const {TabPane} = Tabs;

export const InterviewsPage = props => {
    const [isShareLinkDialog, setShareLinkDialog] = useState(false);
    const [refreshBoard, setRefreshBoard] = useState(false)


    const callback = (key) => {
        console.log(key);
    }

    return (
        <div className="interviews-page page">
            <ShareLinkDialog visible={isShareLinkDialog} onClose={() => {
                setRefreshBoard(true)
                setShareLinkDialog(false)
            }}/>
            <PageHeader
                title="面试房间"
                extra={[
                    <Button
                        size="large"
                        key="btn1"
                        type="primary"
                        onClick={() => {
                            setShareLinkDialog(true)
                        }}
                    >
                        创建新面试
                    </Button>
                ]}
            />
            <div className="page-content">
                <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="我创建的" key="1">
                        <MyCreatedPage
                            refreshBoard={refreshBoard}
                            setRefreshBoard={setRefreshBoard}/>
                    </TabPane>
                    <TabPane tab="我参与的" key="2">
                        <MyTakePartPage
                            refreshBoard={refreshBoard}
                            setRefreshBoard={setRefreshBoard}/>
                    </TabPane>
                </Tabs>
            </div>
        </div>
    );
};
